'use client';

import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { END_POINT } from '@/config';

import '@/styles/item-page.css';

export default function ProductPage() {
  
  const router = useRouter();
  const [product, setProduct] = useState(null);
  const [tempImage, setTempImage] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    fetch(END_POINT.GET_PRODUCT(id), {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(res => {
        console.log(res)
        if (res.status === 404)
          router.replace('/notFound');
        else
          res.json();
      })
      .then(json => setProduct(json.data))
      .catch(error => console.log(error.message));
  }, []);

  useEffect(() => {
    console.log(product);
    if (product) {
      setTempImage(product.images[0]);
    }
  }, [product]);

  return (
    <div className="product-new-page">
      {product && (
        <div className="product-container">

          {/* LEFT SIDE */}
          <div className="images-section">
            <div className="main-image">
              {product && <img src={tempImage} alt="Product Main" />}
            </div>

            <div className="image-slider">
              {product && product.images.map((img, idx) => (
                <img
                  key={idx}
                  src={img}
                  className={tempImage === img ? "active" : ""}
                  onClick={() => setTempImage(img)}
                />
              ))}
            </div>
          </div>

          {/* RIGHT SIDE */}
          <div className="details-section">

            <h1 className="item-title">{product.title}</h1>
            <p className="item-subtitle">Oversized Comfortable Fit</p>

            <p className="item-price">650 EGP</p>

            <div className="divider" />

            <div className="section">
              <p className="label">Description</p>
              <p className="text">
                Premium fabric hoodie, soft material, perfect for cold weather and everyday outfits.
              </p>
            </div>

            <div className="section">
              <p className="label">Available Sizes</p>
              <div className="sizes-group">
                <span>S</span>
                <span>M</span>
                <span>L</span>
                <span>XL</span>
                <span>2XL</span>
              </div>
            </div>

            <div className="bottom-actions">
              <button className="btn-add">Add To Cart</button>
              <button className="btn-buy">Buy Now</button>
            </div>

          </div>
        </div>
      )}
    </div>
  );
}
